import {
    Button,
    Checkbox,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    FormControlLabel,
    FormGroup,
    Grid,
    Paper,
    Radio,
    RadioGroup,
    TextField,
    Typography,
    MenuItem,
    Chip,
    TableContainer,
    Table,
    TableHead,
    TableRow,
    TableCell,
    TableBody
} from '@mui/material';
import makeStyles from '@mui/styles/makeStyles';
import React, { useContext, useEffect, useMemo, useRef, useState } from 'react';
import { Controller, useForm } from 'react-hook-form';
import Layout from '../component/Layout';
import MyButton from '../component/UI/MyButton';
import { SnackbarContext } from '../contextAPI/SnackbarContext';
import { Bar } from "react-chartjs-2";
import Chart from 'chart.js/auto'
import InputGrid from '../component/InputGrid';
import { useHistory } from 'react-router';
import { CategoryContext } from '../contextAPI/CategoryContext';
import AddCandidateForm from '../component/AddCandidateForm';
import AddVoterForm from '../component/AddVoterForm';
import DeleteVotingForm from '../component/DeleteVotingForm';
import { getAllPoll, getNoVoting, getResult, getVotes, mailing, updatePoll } from '../action/votingAction';
import { LoadingContext } from '../contextAPI/LoadingContext';
import { getDateStatus, getDateTime, getLocaleDateTime, getStatus, getExportData, pdfmakedownload } from '../utils/utils';
import { getAllCategory } from '../action/categoryAction';
import MaterialTable from '@material-table/core';
import SaveResult from '../component/SaveResult';
import HowToVoteIcon from '@mui/icons-material/HowToVote';
import DoneIcon from '@mui/icons-material/Done';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import { CSVLink } from 'react-csv';
import { votingResultConfidencePDF, votingResultPDF } from '../utils/pdf';
import DoughnutChart from '../component/DoughnutChart';

const useStyles = makeStyles(theme => ({
    header: {
        marginBottom: '40px'
    },
    headerContent: {
        textTransform: 'uppercase',
        fontWeight: '600',
    },
    paper: {
        padding: '16px',
        marginBottom: '16px',
        marginTop: '20px',
        // width: 'fit-content'
    },
    paperForm: {
        padding: '16px',
        marginBottom: '16px',
        marginTop: '20px',
        width: '900px',
        margin: '0 auto'
    },
    title: {
        marginBottom: '14px'
    },
    flex: {
        display: 'flex',
        justifyContent: 'space-between',
        marginBottom: '40px'
    },
    table: {
        width: '100%',
        backgroundColor: 'white',
        marginTop: '18px',
    },
}))

const Voting = () => {
    const classes = useStyles();
    const history = useHistory();
    const [candidate, setCandidate] = useState([]);
    const [voter, setVoter] = useState([]);
    const [pollArr, setPollArr] = useState([]);
    const { loadingDispatch } = useContext(LoadingContext)
    const { category, categoryDispatch } = useContext(CategoryContext);
    const { openSnackbarDispatch } = useContext(SnackbarContext);
    const [open, setOpen] = useState([]);
    const [editOpen, setEditOpen] = useState([]);
    const [editState, setEditState] = useState(null);
    const [resultState, setResultState] = useState(null);
    const [label, setLabel] = useState([])
    const [quantity, setQuantity] = useState([]);
    const [quantityCurrent, setQuantityCurrent] = useState([]);
    const [quantityPer, setQuantityPer] = useState("")
    const [resultVoting, setResultVoting] = useState([])
    const [votesList, setVotesList] = useState([]);
    const [noVotingList, setNoVotingList] = useState([]);
    const [indexForm, setIndexForm] = useState("")
    const [type, setType] = useState("Biu quyt c s d");

    const {
        handleSubmit,
        control,
        setValue,
        setError,
        clearErrors,
        getValues,
        watch,
        formState: { errors }
    } = useForm();

    const ThoiGianBatDau = useRef({});
    ThoiGianBatDau.current = watch("ThoiGianBatDau", "");
    const SoPhut = useRef({});
    SoPhut.current = watch("ThoiGianNhacNho", 0)

    const handleChangeSelect = (e) => {
        if (e.target.value != 0) {
            clearErrors(e.target.name)
        }
        setValue(e.target.name, e.target.value)
    }

    const handleChangeType = (e) => {
        setValue(e.target.name, e.target.value)
        setType(e.target.value);
    }

    const handleToggle = (data, index) => {
        setEditOpen([]);
        setResultState(data);
        setIndexForm(index)
        let newOpen = [];
        newOpen[index] = !open[index]
        setOpen(newOpen);
    }

    const handleEditToggle = (data, index) => {
        setOpen([])
        setEditState(data);
        setIndexForm(index)
        let newEdit = [];
        newEdit[index] = !editOpen[index]
        setEditOpen(newEdit);
    }

    const fetchAllPoll = async () => {
        const res = await getAllPoll();
        if (res) {
            setPollArr(res);
            loadingDispatch({ type: 'CLOSE_LOADING' })
        }
    }

    useEffect(() => {
        const getResultAPI = async () => {
            const res = await getResult({ id: resultState.MaBieuQuyet })
            console.log(res);
            setResultVoting(res.Data)
            setLabel(res.Data.map(el => `${el.MaSoDangVien} - ${el.HoTen}`));
            setQuantity(res.Data.map(el => el.SoPhieu));
            setQuantityPer(res.SoLuongBieuQuyet + "/" + res.SoLuong)
            setQuantityCurrent(res.SoLuongBieuQuyet)
        }
        const getVotesAPI = async () => {
            const res = await getVotes({ id: resultState.MaBieuQuyet })
            console.log(res);
            setVotesList(res);
        }
        const getNoVotingAPI = async () => {
            const res = await getNoVoting({ id: resultState.MaBieuQuyet })
            console.log(res);
            setNoVotingList(res)
            loadingDispatch({ type: 'CLOSE_LOADING' })
        }
        if (resultState) {
            loadingDispatch({ type: 'OPEN_LOADING' })
            getResultAPI()
            getVotesAPI()
            getNoVotingAPI()
        }
    }, [resultState])

    const onSubmit = async (data) => {
        loadingDispatch({ type: 'OPEN_LOADING' })
        data.UngCuVien = candidate.map(el => el.MaUngCuVien);
        data.NguoiThamGia = voter.map(el => el.MaNguoiThamGia);
        const res = await updatePoll(data);
        if (res.error) {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: res.error.msg,
                    type: "error"
                }
            })
        } else {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: " cp nht!",
                    type: "success"
                }
            })
            fetchAllPoll();
        }

        loadingDispatch({ type: 'CLOSE_LOADING' })
        setEditOpen([]);
    }

    const handleSendEmail = async (id) => {
        loadingDispatch({ type: 'OPEN_LOADING' })
        const res = await mailing({ id });
        if (res) {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: res.msg,
                    type: "success"
                }
            })
        }
        loadingDispatch({ type: 'CLOSE_LOADING' })
    }

    useEffect(() => {
        loadingDispatch({ type: 'OPEN_LOADING' })
        fetchAllPoll();
    }, [])

    useEffect(() => {
        if (editState != null) {
            setValue("MaBieuQuyet", editState.MaBieuQuyet);
            setValue("MucDich", editState.MucDich);
            setValue("TenBieuQuyet", editState.TenBieuQuyet);
            setValue("ThoiGianNhacNho", editState.ThoiGianNhacNho);
            setValue("PhamVi", editState.PhamVi);
            setValue("LoaiBieuQuyet", editState.LoaiBieuQuyet);
            setType(editState.LoaiBieuQuyet);
            setValue("SoPhieuToiDa", editState.SoPhieuToiDa);
            setValue("ThoiGianBatDau", getDateTime(editState.ThoiGianBatDau))
            setValue("ThoiGianKetThuc", getDateTime(editState.ThoiGianKetThuc))
            setCandidate(editState.UngCuVien);
            setVoter(editState.NguoiThamGia)
        }
    }, [editState])

    useEffect(() => {
        getAllCategory(categoryDispatch, "partycell");
        getAllCategory(categoryDispatch, "position");
        getAllCategory(categoryDispatch, "grade");
    }, [])

    const VotingResultForm = () => {

        const [openResult, setOpenResult] = useState(false);
        const [data, setData] = useState([]);

        const getRate = (all, quan) => {
            return `${+(quan / all * 100).toFixed(2)}%`;
        }

        const columns = resultState.LoaiBieuQuyet == "Biu quyt c s d"
            ? [
                { title: "M s ng vin", field: "MaSoDangVien", },
                { title: "H tn", field: "HoTen", },
                { title: "S phiu", field: "SoPhieu", },
                { title: "T l phiu", field: "TiLe", },
            ]
            : [
                { title: "M s ng vin", field: "MaSoDangVien", },
                { title: "H tn", field: "HoTen", },
                { title: "S phiu tn nhim", field: "SoPhieuTinNhiem", },
                { title: "S phiu khng tn nhim", field: "SoPhieuKhongTinNhiem", },
                { title: "T l phiu tn nhim", field: "TiLeTinNhiem", },
                { title: "T l phiu khng tn nhim", field: "TiLeKhongTinNhiem", },
            ]

        const [rows, setRows] = useState(
            resultState.LoaiBieuQuyet == "Biu quyt c s d" ?
                resultVoting.map((el, index) => ({
                    id: index,
                    HoTen: el.HoTen,
                    MaSoDangVien: el.MaSoDangVien,
                    SoPhieu: el.SoPhieu,
                    TiLe: getRate(resultVoting.reduce((a, b) => a + b.SoPhieu, 0), el.SoPhieu)
                }))
                :
                resultVoting.map((el, index) => ({
                    id: index,
                    HoTen: el.HoTen,
                    MaSoDangVien: el.MaSoDangVien,
                    SoPhieuTinNhiem: el.SoPhieu,
                    SoPhieuKhongTinNhiem: quantityCurrent - el.SoPhieu,
                    TiLeTinNhiem: getRate(quantityCurrent, el.SoPhieu),
                    TiLeKhongTinNhiem: getRate(quantityCurrent, quantityCurrent - el.SoPhieu)
                }))
        );

        const [columnsVote, setColumnsVote] = useState([])

        const [rowsVote, setRowsVote] = useState([])

        const [columnsNoVote, setColumnsNoVote] = useState([
            { title: "STT", field: "id", width: 50 },
            { title: "M s ng vin", field: "MaSoDangVien" },
            { title: "H tn", field: "HoTen" },
            { title: "Email", field: "Email" },
            { title: "S in thoi", field: "SoDienThoai" },
        ])

        const [rowsNoVote, setRowsNoVote] = useState([])

        const dataResult = useMemo(() => getExportData(rows, columns))

        const handleExportPDF = () => {
            let dd = resultState.LoaiBieuQuyet == "Biu quyt c s d"
                ? votingResultPDF(rows, resultState, rowsNoVote.length)
                : votingResultConfidencePDF(rows, resultState, rowsNoVote.length)
            pdfmakedownload(dd);
        }

        useEffect(() => {
            let column = [{ title: "S phiu", field: "id", width: 50 }];
            resultVoting.map((el, index) => {
                column.push({
                    title: el.MaSoDangVien + " - " + el.HoTen,
                    field: el.MaSoDangVien,
                    align: 'center',
                    render: (params) => params[el.MaSoDangVien]
                        ? <HowToVoteIcon sx={{ width: '100%' }} color="success" />
                        : ""
                })
            })
            setColumnsVote(column)
            setRowsVote(votesList.map((el, index) => {
                let item = { id: index + 1 };
                Object.keys(el).map((i, index) => { item[i] = el[i] });
                return item
            }))
        }, [votesList])

        useEffect(() => {
            setRowsNoVote(noVotingList.map((el, index) => ({
                id: index + 1,
                MaSoDangVien: el.MaNguoiThamGia,
                HoTen: el.HoTen,
                Email: el.Email,
                SoDienThoai: el.SoDienThoai
            })));
        }, [noVotingList])

        return (
            <Paper className={classes.paper} variant="outlined">
                <Typography textAlign="center" style={{ marginBottom: '40px' }} variant="h5">
                    {resultState.TenBieuQuyet}
                </Typography>
                <Typography marginBottom="8px">Thi gian: <b>{getLocaleDateTime(resultState.ThoiGianBatDau)} - {getLocaleDateTime(resultState.ThoiGianKetThuc)}</b></Typography>
                <Typography marginBottom="8px">Hnh thc biu quyt: <b>{resultState.LoaiBieuQuyet}</b></Typography>
                {resultState.LoaiBieuQuyet == "Biu quyt c s d" &&
                    <Typography marginBottom="8px">S phiu ti a: <b>{resultState.SoPhieuToiDa}</b></Typography>
                }
                <Typography marginBottom="8px">S ngi biu quyt: <b>{quantityPer}</b></Typography>
                {
                    resultState.LoaiBieuQuyet == "Biu quyt c s d"
                        ?
                        <Bar
                            width="500px"
                            data={{
                                labels: label
                                ,
                                datasets: [
                                    {
                                        label: "S phiu",
                                        backgroundColor: [
                                            "#EF5350",
                                            "#42A5F5",
                                            "#FFEE58",
                                            "#EC407A",
                                            "#7E57C2",
                                            "#66BB6A",
                                            "#26A69A",
                                            "#78909C",
                                            "#AB47BC",
                                            "#9CCC65",
                                            "#FFA726",
                                            "#5C6BC0",
                                            "#8D6E63",
                                        ],
                                        data: quantity
                                    }
                                ]
                            }}
                            options={{
                                legend: { display: true },
                                title: {
                                    display: true,
                                    text: "Predicted world population (millions) in 2050"
                                },
                            }}
                        />
                        :
                        <Grid container spacing={2}>
                            {resultVoting.map(el =>
                                <Grid item xs={3} key={el.MaSoDangVien}>
                                    <DoughnutChart
                                        label={el.HoTen + " - " + el.MaSoDangVien}
                                        data={[
                                            { label: "Tn nhim", quantity: el.SoPhieu },
                                            { label: "Khng tn nhim", quantity: quantityCurrent - el.SoPhieu },
                                        ]}
                                        twoColor
                                    />
                                </Grid>
                            )}
                        </Grid>
                }

                <TableContainer sx={{ width: '1000px', margin: '0 auto', mt: 5, mb: 5 }} variant="outlined">
                    {dataResult.data.length > 0 &&
                        <>
                            <CSVLink data={dataResult.data} headers={dataResult.headers} filename={`${resultState.TenBieuQuyet}.csv`}>
                                <MyButton style={{ marginLeft: 8 }} success>
                                    <FileDownloadIcon style={{ marginRight: 4 }} />Excel
                                </MyButton>
                            </CSVLink>
                            <MyButton onClick={handleExportPDF} sx={{ ml: 1, backgroundColor: "#e95340", '&:hover': { backgroundColor: '#e95340' } }}>
                                <FileDownloadIcon sx={{ mr: 0.5 }} />pdf
                            </MyButton>
                        </>
                    }
                    <MaterialTable
                        components={{
                            Container: (props) =>
                                <Paper
                                    {...props}
                                    className={classes.table}
                                    variant="outlined"
                                />
                        }}
                        title={
                            <Grid container alignItems="center">
                                <Typography variant="button">Bng kt qu</Typography>
                                {
                                    resultState.LuuKetQua == 1 &&
                                    <Chip
                                        sx={{ marginLeft: 3 }}
                                        size="small"
                                        varian="outlined"
                                        color="success"
                                        label={" lu"}
                                        icon={<DoneIcon />}
                                    />
                                }
                            </Grid>
                        }
                        columns={columns}
                        data={rows}

                        actions={[
                            (resultState.LuuKetQua == 0 && resultState.MucDich != "Khc") &&
                            {
                                // isFreeAction: true,
                                icon: 'save',
                                tooltip: 'Lu',
                                onClick: (event, rowData) => {
                                    setData(rowData);
                                    setOpenResult(true)
                                },
                            },
                        ]}

                        options={{
                            sorting: false,
                            search: false,
                            paging: false,
                            padding: 'dense',
                            selection: resultState.LuuKetQua == 0 && resultState.MucDich != "Khc"
                        }}
                    />
                </TableContainer>
                <SaveResult
                    open={openResult}
                    setOpen={setOpenResult}
                    data={data}
                    resultState={resultState}
                    setResultState={setResultState}
                />
                {resultState.LoaiBieuQuyet == "Biu quyt c s d" &&
                    <TableContainer sx={{ width: '1000px', margin: '0 auto', mb: 5 }} variant="outlined">
                        <MaterialTable
                            components={{
                                Container: (props) =>
                                    <Paper
                                        {...props}
                                        className={classes.table}
                                        variant="outlined"
                                    />
                            }}
                            title={<Typography variant="button">Bng thng k phiu</Typography>}
                            columns={columnsVote}
                            data={rowsVote}

                            options={{
                                sorting: false,
                                search: false,
                                paging: false,
                                padding: 'dense',
                            }}
                        />
                    </TableContainer>
                }

                <TableContainer sx={{ width: '1000px', margin: '0 auto', mb: 5 }} variant="outlined">
                    <MaterialTable
                        components={{
                            Container: (props) =>
                                <Paper
                                    {...props}
                                    className={classes.table}
                                    variant="outlined"
                                />
                        }}
                        title={"Bng lit k ng vin khng biu quyt"}
                        columns={columnsNoVote}
                        data={rowsNoVote}

                        options={{
                            sorting: false,
                            search: false,
                            paging: false,
                            padding: 'dense',
                        }}
                    />
                </TableContainer>
            </Paper >
        )
    }

    const checkEditOpen = (index) => {
        return (editOpen.length > 0 && index == indexForm) ? !editOpen.every(el => el == false) : false
    }
    const checkOpen = (index) => {
        return (open.length > 0 && index == indexForm) ? !open.every(el => el == false) : false
    }
    return (
        <>
            <Layout sidebar>
                <div className={classes.header} >
                    <Typography className={classes.headerContent} variant="h5">
                        Qun l Biu quyt
                    </Typography>
                </div>
                <Grid style={{ width: '100%' }} container spacing={2}>
                    {pollArr.length > 0 ?
                        pollArr.map((el, index) =>
                            <React.Fragment key={el.MaBieuQuyet}>
                                <Grid item xs={6}>
                                    <Paper className={classes.paper} variant="outlined">
                                        <Grid container justifyContent="space-between" marginBottom="40px">
                                            <Typography variant="button">
                                                {getLocaleDateTime(el.ThoiGianBatDau)} - {getLocaleDateTime(el.ThoiGianKetThuc)}
                                            </Typography>
                                            <Typography color="gray" variant="button">{getDateStatus(el.ThoiGianBatDau, el.ThoiGianKetThuc)}</Typography>
                                        </Grid>
                                        <Typography textAlign="center" className={classes.title} variant="h5">
                                            {el.TenBieuQuyet}
                                        </Typography>
                                        <Typography textAlign="center" className={classes.title}>
                                            Phm vi: <b>{el.PhamVi}</b>
                                        </Typography>
                                        {
                                            el.LoaiBieuQuyet == "Biu quyt c s d" ?
                                                <Typography textAlign="center" className={classes.title}>
                                                    S phiu ti a: <b>{el.SoPhieuToiDa}</b>
                                                </Typography>
                                                :
                                                <Typography textAlign="center" className={classes.title}>
                                                    {el.LoaiBieuQuyet}
                                                </Typography>
                                        }
                                        <Grid container justifyContent="center">
                                            {getStatus(el.ThoiGianBatDau, el.ThoiGianKetThuc) == 2 &&
                                                <MyButton onClick={() => handleToggle(el, index)} primary style={{ marginBottom: '20px', marginLeft: "8px" }}>
                                                    {open[index] ? 'n' : 'Xem kt qu'}
                                                </MyButton>
                                            }
                                            {/* {getStatus(el.ThoiGianBatDau, el.ThoiGianKetThuc) == 1 &&
                                                <>
                                                    {editOpen[index] ?
                                                        <Button onClick={() => handleEditToggle(el, index)} variant='outlined' style={{ marginBottom: '20px', marginLeft: "8px" }}>
                                                            Hy
                                                        </Button>
                                                        :
                                                        <MyButton onClick={() => handleEditToggle(el, index)} primary style={{ marginBottom: '20px', marginLeft: "8px" }}>
                                                            Chnh sa
                                                        </MyButton>
                                                    }
                                                </>
                                            } */}
                                            {getStatus(el.ThoiGianBatDau, el.ThoiGianKetThuc) != 2 &&
                                                <>
                                                    {
                                                        getStatus(el.ThoiGianBatDau, el.ThoiGianKetThuc) == 0 &&
                                                        (
                                                            editOpen[index] ?
                                                                <Button onClick={() => handleEditToggle(el, index)} variant='outlined' style={{ marginBottom: '20px', marginLeft: "8px" }}>
                                                                    Hy
                                                                </Button>
                                                                :
                                                                <MyButton onClick={() => handleEditToggle(el, index)} primary style={{ marginBottom: '20px', marginLeft: "8px" }}>
                                                                    Chnh sa
                                                                </MyButton>
                                                        )
                                                    }
                                                    <MyButton onClick={() => handleSendEmail(el.MaBieuQuyet)} primary style={{ marginBottom: '20px', marginLeft: "8px" }}>
                                                        Gi mail
                                                    </MyButton>
                                                </>
                                            }
                                            <DeleteVotingForm data={el} fetchAllPoll={fetchAllPoll} />
                                        </Grid>

                                    </Paper>
                                </Grid>
                                {checkOpen(index) &&
                                    <Grid item xs={12}>
                                        <VotingResultForm />
                                    </Grid>
                                }
                                {checkEditOpen(index) &&
                                    <Grid item xs={12}>
                                        <Paper className={classes.paperForm} variant="outlined">
                                            <Typography style={{ textTransform: 'uppercase', marginBottom: 30 }}>Chnh sa biu quyt</Typography>
                                            <Grid container className={classes.inputItem} alignItems="center">
                                                <Grid item xs={4}>
                                                    <Typography>Tn biu quyt</Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <InputGrid
                                                        noTitle
                                                        name="TenBieuQuyet"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "Vui lng nhp trng ny!",
                                                        }}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <Grid container className={classes.inputItem} alignItems="center">
                                                <Grid item xs={4}>
                                                    <Typography>Mc ch</Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <InputGrid
                                                        select
                                                        noTitle
                                                        onChange={handleChangeSelect}
                                                        name="MucDich"
                                                        defaultValue="0"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            validate: value =>
                                                                value != 0 || "Vui lng nhp trng ny!"
                                                        }}
                                                    >
                                                        <MenuItem value="0">Chn</MenuItem>
                                                        <MenuItem value="Khen thng">Khen thng</MenuItem>
                                                        <MenuItem value="K lut">K lut</MenuItem>
                                                        <MenuItem value="Khc">Khc</MenuItem>
                                                    </InputGrid>
                                                </Grid>
                                            </Grid>
                                            <Grid container className={classes.inputItem} alignItems="center">
                                                <Grid item xs={4}>
                                                    <Typography>Loi biu quyt</Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <InputGrid
                                                        select
                                                        noTitle
                                                        onChange={handleChangeType}
                                                        name="LoaiBieuQuyet"
                                                        defaultValue="Biu quyt c s d"
                                                        control={control}
                                                        errors={errors}
                                                    >
                                                        <MenuItem value="Biu quyt c s d">Biu quyt c s d</MenuItem>
                                                        <MenuItem value="Biu quyt tn nhim">Biu quyt tn nhim</MenuItem>
                                                    </InputGrid>
                                                </Grid>
                                            </Grid>
                                            {
                                                type == "Biu quyt c s d" &&
                                                <Grid container className={classes.inputItem} alignItems="center" >
                                                    <Grid item xs={4}>
                                                        <Typography>S phiu ti a</Typography>
                                                    </Grid>
                                                    <Grid item xs={8}>
                                                        <InputGrid
                                                            noTitle
                                                            type="number"
                                                            defaultValue="1"
                                                            name="SoPhieuToiDa"
                                                            control={control}
                                                            errors={errors}
                                                            InputProps={{ inputProps: { min: 1 } }}
                                                        />
                                                    </Grid>
                                                </Grid>
                                            }
                                            <Grid container className={classes.inputItem} alignItems="center">
                                                <Grid item xs={4}>
                                                    <Typography>Thi gian bt u</Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <InputGrid
                                                        noTitle
                                                        type="datetime-local"
                                                        name="ThoiGianBatDau"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "Vui lng nhp trng ny!",
                                                        }}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <Grid container className={classes.inputItem} alignItems="center">
                                                <Grid item xs={4}>
                                                    <Typography>Thi gian kt thc</Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <InputGrid
                                                        noTitle
                                                        type="datetime-local"
                                                        name="ThoiGianKetThuc"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "Vui lng nhp trng ny!",
                                                            validate: value => {
                                                                if (new Date(value) < new Date(ThoiGianBatDau.current))
                                                                    return "Thi gian kt thc phi ln hn thi gian bt u"
                                                                if (new Date(value) < new Date())
                                                                    return "Thi gian kt thc phi hn thi gian hin ti"
                                                                let date = new Date(value);
                                                                date.setMinutes(date.getMinutes() - SoPhut.current);
                                                                if (date < new Date()) {
                                                                    return " qu thi gian nhc nh"
                                                                }
                                                                return true
                                                            }
                                                        }}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <Grid container className={classes.inputItem} alignItems="center" >
                                                <Grid item xs={4}>
                                                    <Typography>Phm vi</Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <InputGrid
                                                        noTitle
                                                        name="PhamVi"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "Vui lng nhp trng ny!",
                                                        }}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <Grid container className={classes.inputItem} alignItems="center" >
                                                <Grid item xs={4}>
                                                    <Typography>Thi gian nhc nh (pht)</Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <InputGrid
                                                        noTitle
                                                        type="number"
                                                        InputProps={{ inputProps: { min: 10 } }}
                                                        defaultValue="10"
                                                        name="ThoiGianNhacNho"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "Vui lng nhp trng ny!",
                                                        }}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <Grid container style={{ marginTop: '16px' }} >
                                                <Grid item xs={4}>
                                                    <Typography>ng c vin</Typography>
                                                </Grid>
                                                <Grid item container xs={8}>
                                                    {
                                                        candidate.length > 0 &&
                                                        candidate.map(el =>
                                                            <Grid key={el.MaUngCuVien} item xs={12}>
                                                                <Chip style={{ marginBottom: '12px' }}
                                                                    size="medium"
                                                                    varian="outlined"
                                                                    label={el.HoTen + " - " + el.MaUngCuVien}
                                                                />
                                                            </Grid>
                                                        )
                                                    }
                                                    <Grid item xs={12}>
                                                        <AddCandidateForm setCandidate={setCandidate} />
                                                    </Grid>
                                                </Grid>
                                            </Grid>
                                            <Grid container style={{ marginTop: '16px' }} >
                                                <Grid item xs={4}>
                                                    <Typography>n v tham gia</Typography>
                                                </Grid>
                                                <Grid item container xs={8}>
                                                    <Grid item xs={12}>
                                                        {
                                                            voter.length > 0 &&
                                                            voter.map(el =>
                                                                <Chip key={el.MaNguoiThamGia} style={{ marginBottom: '12px', marginRight: '4px' }}
                                                                    size="small"
                                                                    varian="outlined"
                                                                    label={el.HoTen + " - " + el.MaNguoiThamGia}
                                                                />
                                                            )
                                                        }
                                                    </Grid>
                                                    <Grid item xs={12}>
                                                        <AddVoterForm setVoter={setVoter} />
                                                    </Grid>
                                                </Grid>
                                            </Grid>
                                            <Grid container style={{ marginTop: '16px' }} >
                                                <Grid item xs={4}>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <MyButton onClick={handleSubmit(onSubmit)} info>Lu</MyButton>
                                                    <Button onClick={() => handleEditToggle(el, index)} variant='outlined' style={{ marginLeft: "8px" }}>
                                                        Hy
                                                    </Button>
                                                </Grid>
                                            </Grid>
                                        </Paper>
                                    </Grid>
                                }
                            </React.Fragment>
                        )
                        :
                        <Typography>Khng c cuc biu quyt no ang din ra</Typography>
                    }
                </Grid>
            </Layout>
        </>
    );
};

export default Voting;