import { getLocaleDate, getLocaleDateTime } from "./utils"

const exportDD = (body, title, pageOrientation = "portrait") => {
    return {
        pageOrientation: pageOrientation,
        content: [
            {
                columns: [
                    {
                        text: [
                            'NG B I HC CN TH \n',
                            'CHI B KHOA CNTT&TT'
                        ],
                        alignment: 'center'
                    },
                    {
                        text: [
                        ],
                    },
                    [
                        {
                            text: 'NG CNG SN VIT NAM \n',
                            alignment: 'center'
                        },
                        {
                            text: `Ninh Kiu, ngy ${(new Date()).getDate()} thng ${(new Date()).getMonth() + 1} nm ${(new Date()).getFullYear()} \n`,
                            alignment: 'center'
                        }
                    ]
                ],
            },
            {
                text: `${title} \n`,
                alignment: 'center',
                style: 'header',
                bold: true,
                margin: [0, 24, 0, 24]
            },
            {
                style: 'tableExample',
                color: '#222',
                table: {
                    // widths: ['auto', 'auto', 'auto'],
                    headerRows: 2,
                    // keepWithHeaderRows: 1,
                    widths: body.widths,
                    body: body.body
                }
            },
        ],
        styles: {
            header: {
                fontSize: 14,
                alignment: 'justify'
            },
            tableExample: {
                margin: [0, 5, 0, 15]
            },
            content: {
                margin: [0, 30, 0, 0],
            },
            tableHeader: {
                bold: true,
                fontSize: 13,
                color: 'black'
            }
        }

    }
}

export const partyMemberPDF = (data, title = "DANH SCH NG VIN") => {
    let body = {
        widths: ['auto', '*', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto'],
        body: [
            [
                { text: 'STT', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'H tn', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'M s ng vin', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'Gii tnh', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'Ngy sinh', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'Ni sinh', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'Ngy vo ng', style: 'tableHeader', alignment: 'center', colSpan: 2 },
                { text: "" },
                { text: 'Ni vo ng', style: 'tableHeader', alignment: 'center', colSpan: 2 },
                { text: "" },
                { text: 'S th', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'Chc v', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'Dn tc', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
                { text: 'Tn gio', style: 'tableHeader', alignment: 'center', rowSpan: 2 },
            ],
            ['', '', '', "", "", "", { text: "Ln u", style: 'tableHeader' }, { text: "Chnh thc", style: 'tableHeader' }, { text: "Ln u", style: 'tableHeader' }, { text: "Chnh thc", style: 'tableHeader' }, "", "", "", ""],
        ]
    }

    data.map((el, index) => {
        body.body.push([
            index + 1, el.HoTen, el.MaSoDangVien, el.TenGioiTinh,
            getLocaleDate(el.NgaySinh), el.NoiSinh, getLocaleDate(el.NgayVaoDang), getLocaleDate(el.NgayChinhThuc), el.NoiVaoDangLanDau, el.NoiVaoDangChinhThuc, el.SoThe, el.TenChucVu, el.TenDanToc, el.TenTonGiao])
    })

    return exportDD(body, title, "landscape")
}

