export const moveOutPDF = (data, title) => {
    let body = {
        widths: ['auto', '*', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto'],
        body: [
            [
                { text: 'STT', style: 'tableHeader', alignment: 'center', },
                { text: 'H tn', style: 'tableHeader', alignment: 'center', },
                { text: 'M s ng vin', style: 'tableHeader', alignment: 'center', },
                { text: 'Hnh thc', style: 'tableHeader', alignment: 'center', },
                { text: 'Chuyn t', style: 'tableHeader', alignment: 'center', },
                { text: 'Chuyn n', style: 'tableHeader', alignment: 'center', },
                { text: 'Ngy chuyn i', style: 'tableHeader', alignment: 'center', },
                { text: 'Ngy chuyn v', style: 'tableHeader', alignment: 'center', },
                { text: 'Ghi ch', style: 'tableHeader', alignment: 'center', },
            ],
        ]
    }

    data.map((el, index) => {
        body.body.push([
            index + 1, el.HoTen, el.MaSoDangVien, el.TenHinhThuc,
            "ng b " + el.ChuyenTuDangBo + ", chi b " + el.TenChiBoTu,
            "ng b " + el.ChuyenDenDangBo + ", chi b " + el.TenChiBoDen,
            getLocaleDate(el.NgayChuyenDi), el.NgayChuyenDen ? getLocaleDate(el.NgayChuyenDen) : "", el.GhiChu
        ])
    })

    return exportDD(body, title)
}

